@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.ReactDates-overrides .DayPicker {
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-weight: bold;
  background: none;
}

.ReactDates-overrides .DateRangePicker {
  position: absolute;
  z-index: 0;
  font-family: "Assistant", sans-serif;
}

.ReactDates-overrides .CalendarDay {
  border: none;
  color: #a2a2a2;
  font-size: 14px;
  height: 25px !important;

  /* stylelint-disable-line */
  font-weight: normal;
  -webkit-transition: color 150ms ease-out,
    border-radius 150ms ease-out,
    background-color 150ms ease-out;
  transition: color 150ms ease-out,
    border-radius 150ms ease-out,
    background-color 150ms ease-out;
  will-change: color background-color;
}

.ReactDates-overrides .DateRangePickerInput {
  border-width: 0;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.ReactDates-overrides .DateRangePicker,
  .ReactDates-overrides .DateRangePicker > div {
  height: 100%;
}

.ReactDates-overrides .DateRangePicker__picker--open-down {
  top: 45px;
}

.ReactDates-overrides .DayPickerNavigation__next,
  .ReactDates-overrides .DayPickerNavigation__prev {
  color: #37cc9a;
  border: none;
  background-color: transparent;
  font-size: 18px;
}

.ReactDates-overrides .CalendarMonth .CalendarMonth__caption {
  opacity: 0;
  color: #a2a2a2;
  padding: 15px 0 48px;
  font-size: 14px;
  text-transform: uppercase;
  -webkit-transition: opacity 100ms ease-in-out;
  transition: opacity 100ms ease-in-out;
}

.ReactDates-overrides .CalendarMonth table {
  position: relative;
  border-collapse: separate;
  border-spacing: 0 3px;
}

.ReactDates-overrides .CalendarMonth table:before {
  content: "";
  display: block;
  height: 220px;
  width: 1px;
  background-color: rgba(#000000 0.05);
  -webkit-transform: translateX(-12px) translateY(-70px);
          transform: translateX(-12px) translateY(-70px);
  position: absolute;
  opacity: 0;
  -webkit-transition: opacity 100ms ease-in-out;
  transition: opacity 100ms ease-in-out;
}

.ReactDates-overrides .CalendarMonth[data-visible="true"] table:before {
  opacity: 1;
}

.ReactDates-overrides .CalendarMonth[data-visible="false"] + .CalendarMonth table:before {
  opacity: 0;
}

.ReactDates-overrides .CalendarMonth[data-visible="true"] .CalendarMonth__caption {
  opacity: 1;
}

.ReactDates-overrides .CalendarMonth[data-visible="false"]:last-child .CalendarMonth__caption {
  opacity: 0;
}

.ReactDates-overrides .DayPicker__week-header {
  top: 53px;
}

.ReactDates-overrides .DayPicker__week-header ul {
  padding-top: 4px;
  padding-bottom: 5px;
  background-color: #f6f6f6;
  color: #37cc9a;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
}

.ReactDates-overrides .DayPickerNavigation {
  color: #37cc9a;
}

.ReactDates-overrides .DayPickerNavigation__prev {
  left: 10px;
}

.ReactDates-overrides .DayPickerNavigation__next {
  right: 10px;
}

.ReactDates-overrides .CalendarDay--blocked-out-of-range {
  color: #bababa;
}

.ReactDates-overrides .CalendarMonthGrid {
  background: none;
}

.ReactDates-overrides .CalendarDay--selected-span {
  background-color: #eeeeee;
}

.ReactDates-overrides .CalendarDay--selected-start,
  .ReactDates-overrides .CalendarDay--selected-end,
  .ReactDates-overrides .CalendarDay--selected {
  background-color: #37cc9a;
  color: #ffffff;
  font-weight: bold;
}

.ReactDates-overrides .CalendarDay--selected {
  border-radius: 3px;
}

.ReactDates-overrides .CalendarDay--selected-start {
  border-radius:
      3px
      0
      0
      3px;
}

.ReactDates-overrides .CalendarDay--selected-end {
  border-radius:
      0
      3px
      3px
      0;
}

.ReactDates-overrides .CalendarDay--hovered-span:not(.CalendarDay--selected-start),
  .ReactDates-overrides .CalendarDay--hovered.CalendarDay--selected-span {
  background-color: #c3f0e0;
}

.ReactDates-overrides .CalendarDay:active {
  background-color: lighten(#37cc9a, 30%);
  border-radius: 3px;
}



