@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.tab {
  display: inline-block;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: none;
  font-family: "Assistant", sans-serif;
  font-weight: 600;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  cursor: pointer;
  text-align: center;
  width: 200px;
  color: #757575;
  background: #f6f6f6;
  padding: 12px;
  font-size: 16px;
}

.tab > input {
  visibility: hidden;
  position: absolute;
}

.icon {
  margin-bottom: 5px;
  font-size: 20px;
}

.selected {
  color: #37cc9a;
  background: #ffffff;
  border-top: solid 2px #37cc9a;
  padding-top: 10px;
}

.tabBar {
  border: solid 1px #f6f6f6;
  border-top: none;
  display: inline-block;
  padding: 20px;
  padding-top: 0;
}

.tabBar * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.tabs {
  margin: 0 -20px;
}




