.card {
  height: 150px;
  background-image:
    -webkit-gradient(
      linear,
      right bottom, left top,
      color-stop(50%, #f3f3f3),
      color-stop(50%, #ffffff),
      to(#ffffff)
    );
  background-image:
    linear-gradient(
      to top left,
      #f3f3f3 50%,
      #ffffff 50%,
      #ffffff
    )
}
.card p {
  margin: 0;
}
.cardContent {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding:
    16px
    24px
}
.cardContent > * {
  width: 100%;
}
.cardContent > div:first-of-type {
  padding: 0;
}
.cardNumber {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  word-spacing: 8px;
}
.cardChip {
  margin-right: 8px;
  margin-right: 16px;
}
.cardBrandHolder {
  height: 35px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between
}
.cardBrandHolder p {
  font-size: 14px;
  text-transform: uppercase;
}




