@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.segmentedSwitch {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  border-radius: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 1px solid #1f1f1f;
  background-color: #1f1f1f;
  -webkit-box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, .55);
          box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, .55);
}

.item {
  position: relative;
}

.item > input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.label {
  background-color: #1f1f1f;
  padding: 5px 14px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.1em;
  font-family: "Assistant", sans-serif;
  cursor: pointer;
  font-size: 10px;
  color: #757575;
  display: block;
  position: relative;
}

.item input:checked + .label {
  background-color: #37cc9a;
  color: #1f1f1f;
}

.item:first-child input:checked + .label {
  border-radius:
    3px
    0
    0
    3px;
}

.item:last-child input:checked + .label {
  border-radius:
    0
    3px
    3px
    0;
}



