.event {
  outline: none;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.event:first-of-type {
  border-radius: 3px 3px 0 0;
}
.event:last-of-type {
  padding-bottom: 24px;
}
.event:not(:only-child):after {
  content: "";
  bottom: 0;
  left: 40px;
  position: absolute;
  width: 2px;
  height: 100%;
  display: block;
  background-color: #c2c5c7;
}
.event:first-of-type:after {
  height: calc(100% - 32px);
}
.event:last-of-type:after {
  top: 0;
  bottom: auto;
  height: 32px;
}
.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  padding: 0 24px;
}
.number {
