  height: 34px;
  display: block;
  z-index: 2;
  position: relative;
  line-height: 33px;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background-color: #c2c5c7;
}
.title {
  font-size: 14px;
  font-weight: normal;
  text-transform: uppercase;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
