}
.info {
  padding-right: 24px;
  padding-left: 94px
}
.info section {
  padding: 16px 0;
}
.info section:not(:last-of-type) {
  border-bottom: 1px dashed currentColor;
}
.info section:first-of-type {
  padding-top: 0;
}
.info p {
  margin-top: 0;
}
.info p:last-of-type {
