  margin-left: 94px
}
.indicator:before {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  top: 50%;
  position: absolute;
  background-color: #c2c5c7;
}
.indicator span {
  z-index: 2;
  position: relative;
  width: 20px;
  height: 20px;
