          justify-content: center;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background-color: #c2c5c7;
}
.active {
  color: #ffffff;
  padding-bottom: 24px
}
.active .header,
  .active + .event .header {
  padding-top: 16px;
}
.active .number,
  .active .indicator span,
  .active .indicator:before {
  background-color: #ffffff;
}
.active .title,
  .active .title:before {
  color: inherit;
}
.active:not(:only-child):after {
  background-color: #ffffff;
}



