@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.switch {
  cursor: pointer;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  padding-left: 55px;
  height: 21px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: "Assistant", sans-serif;
}

.switch,
.switch:before,
.switch:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.switch:before,
.switch:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  pointer-events: none;
}

.switch:before {
  background-color: #37cc9a;
  width: 11px;
  height: 11px;
  z-index: 1;
  -webkit-transform:
    translateX(
      5px
    )
    translateY(
      5.5px
    );
          transform:
    translateX(
      5px
    )
    translateY(
      5.5px
    );
  -webkit-transition: -webkit-transform 100ms ease-in;
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
}

.switch:after {
  background-color: #ffffff;
  width: 40px;
  height: 21px;
  border: 1px solid #a0a0a0;
  -webkit-transition: background-color 70ms ease-in;
  transition: background-color 70ms ease-in;
}

.checked:before {
  background-color: #ffffff;
  -webkit-transform:
    translateX(
      24px
    )
    translateY(
      5.5px
    );
          transform:
    translateX(
      24px
    )
    translateY(
      5.5px
    );
}

.switch > input {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.checked:after {
  background-color: #37cc9a;
  border-color: #37cc9a;
}

.checked {
  color: #37cc9a;
}

.disabled {
  color: #a0a0a0;
}

.disabled:before {
  background-color: #a0a0a0;
}

.disabled:after {
  background-color: #ffffff;
  border-color: #a0a0a0;
}

.switch span::-moz-selection {
  color: inherit;
}

.switch span::selection {
  color: inherit;
}

.checked span::-moz-selection {
  color: #37cc9a;
}

.checked span::selection {
  color: #37cc9a;
}



