@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.input {
  background-color: #ffffff;
  border: var(--input-border);
  border-color: #c2c5c7;
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-family: "Assistant", sans-serif;
  font-size: 14px;
  min-height: 32px;
  position: relative;
  -webkit-transition: border-color 100ms ease-in-out;
  transition: border-color 100ms ease-in-out;
  width: 100%;
  z-index: 1;
}

.input,
.input * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.boxContainer {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.container {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 5px;
  position: relative;
}

.container * {
  font-family: "Assistant", sans-serif;
  letter-spacing: 0.02em;
}

.container > textarea,
.container > input {
  background: none;
  border: none;
  color: #757575;
  display: block;
  font-size: 14px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0;
  padding: 0;
}

.container > textarea {
  height: 100%;
  margin: 0;
  overflow: hidden;
  position: absolute;
  resize: none;
  width: 100%;
  z-index: 1;
}

.container > textarea::-webkit-input-placeholder,
.container > input::-webkit-input-placeholder {
  color: #a0a0a0;
  font-size: 14px;
}

.container > textarea:-ms-input-placeholder,
.container > input:-ms-input-placeholder {
  color: #a0a0a0;
  font-size: 14px;
}

.container > textarea::-ms-input-placeholder,
.container > input::-ms-input-placeholder {
  color: #a0a0a0;
  font-size: 14px;
}

.container > textarea::placeholder,
.container > input::placeholder {
  color: #a0a0a0;
  font-size: 14px;
}

.icon {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding-left: 10px;
}

.icon svg {
  color: #a0a0a0;
  width: 16px;
}

.displayPasswordIcon {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  outline: none;
}

.multiline {
  height: auto;
}

.multiline > .expander {
  visibility: hidden;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.multiline > .expander,
.multiline textarea {
  font-size: 14px;
  line-height: 20px;
  word-break: break-word;
}

/* focus and hover */

.container > textarea:focus,
.container > input:focus {
  outline: none;
}

.focused,
.input:hover {
  border-color: #37cc9a;
}

/* error */

.error,
.error:hover {
  border-color: #c2143e;
}

/* disabled */

.disabled,
.disabled:hover {
  border-color: #d1d5d8;
}

.container > input:disabled,
.container > textarea:disabled,
.container > input:disabled::-webkit-input-placeholder,
.container > textarea:disabled::-webkit-input-placeholder {
  cursor: not-allowed;
  color: #d1d5d8;
}

.container > input:disabled,
.container > textarea:disabled,
.container > input:disabled:-ms-input-placeholder,
.container > textarea:disabled:-ms-input-placeholder {
  cursor: not-allowed;
  color: #d1d5d8;
}

.container > input:disabled,
.container > textarea:disabled,
.container > input:disabled::-ms-input-placeholder,
.container > textarea:disabled::-ms-input-placeholder {
  cursor: not-allowed;
  color: #d1d5d8;
}

.container > input:disabled,
.container > textarea:disabled,
.container > input:disabled::placeholder,
.container > textarea:disabled::placeholder {
  cursor: not-allowed;
  color: #d1d5d8;
}

.disabled .icon svg {
  color: #d1d5d8;
}



