.recipient > div {
  height: 100%;
}

.recipient .summary,
  .recipient .description {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

.row {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

.total {
  font-size: 24px;
  font-weight: bolder;
}

.title {
  font-weight: bold
}

.title:before {
  counter-increment: section;
  padding-right: 5px;
  content: counter(section) ". ";
}

.summary {
  text-transform: uppercase;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}

.summary > * {
  text-align: right;
}

.summary > span:first-child {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}

.summary .amount {
  font-weight: bold;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 1;
}

.statusLabel {
  margin-right: 8px;
}



