.recipientSection {
  -webkit-box-shadow: none;
          box-shadow: none;
  border: solid 1px #d1d5d8;
  background-color: #f3f3f3
}

.recipientSection table,
  .recipientSection table tr {
  border: none;
}

.recipientSection table th {
  background-color: #f3f3f3;
}

.divider {
  display: block;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #d1d5d8;
  margin: 0;
}



