@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.linearProgress,
.linearProgress * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.linearProgress {
  width: 300px;
  height: 25px;
  position: relative;
  overflow: hidden;
}

.background {
  height: 6px;
  background-color: #858c8f;
}

.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  will-change: width;
  height: 6px;
  background-image: -webkit-gradient(
      linear,
      left top, right top,
      color-stop(10%, #fc8a5d),
      color-stop(90%, #ff796f)
    );
  background-image: linear-gradient(
      90deg,
      #fc8a5d 10%,
      #ff796f 90%
    );
}

.number {
  position: relative;
  height: 100%;
  font-family: "Assistant", sans-serif;
  text-align: right;
  width: 0%;
  display: inline-block;
  color: #c2143e;
}

.disabled .back {
  background-color: #858c8f;
}

.disabled .fill {
  background-color: #454545;
  background-image: none;
}

.disabled .number {
  color: #454545;
}



