/* stylelint-disable selector-max-class */
@font-face {
  font-family: "Assistant";
  src:
    url("./../../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}
@font-face {
  font-family: "Assistant";
  src:
    url("./../../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
/* stylelint-disable */
body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}
body * {
  letter-spacing: 0.02em;
}
h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}
h1 {
  font-size: 24px;
  margin: 24px 0;
}
h2 {
  font-size: 20px;
  margin: 24px 0;
}
h3 {
  font-size: 18px;
  margin: 16px 0;
}
p {
  margin-bottom: 1em;
}
a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}
a:hover {
  text-decoration: underline;
}
ul,
ol {
  padding: 0 24px;
}
li {
  list-style-type: disc;
}
blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}
/* stylelint-enable */
.input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.input,
.input * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.boxContainer {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.container {
  padding-top: 4px;
  position: relative;
}
.container * {
  font-family: "Assistant", sans-serif;
  letter-spacing: 0.02em;
}
.container > textarea,
.container > input {
  background: none;
  border-color: #c2c5c7;
  border-radius: 3px;
  border-style: solid;
  border-width: 0 0 2px 0;
  color: #757575;
  caret-color: #a0a0a0;
  display: block;
  font-size: 14px;
  min-width: 100%;
}
.container > input {
  padding-top: 16px;
  padding-bottom: 8px;
}
.container > textarea {
  padding: 0;
  margin: 0;
  resize: none;
  z-index: 1;
  position: absolute;
  height: calc(100% - 25px);
  overflow: hidden;
}
.container > textarea::-webkit-input-placeholder,
.container > input::-webkit-input-placeholder {
  opacity: 0;
}
.container > textarea:-ms-input-placeholder,
.container > input:-ms-input-placeholder {
  opacity: 0;
}
.container > textarea::-ms-input-placeholder,
.container > input::-ms-input-placeholder {
  opacity: 0;
}
.container > textarea::placeholder,
.container > input::placeholder {
  opacity: 0;
}
.container > label {
  color: #a0a0a0;
  font-size: 14px;
  pointer-events: none;
  position: absolute;
  top: 20px;
  -webkit-transition: 0.1s ease all;
  transition: 0.1s ease all;
  white-space: nowrap;
}
.container .contentPresent {
  font-size: 12px;
  top: 0;
}
.secondaryText {
  color: #a0a0a0;
  font-size: 12px;
  margin: 0;
  margin-top: 8px;
  text-align: left;
  width: 100%;
}
/* multiline */
.multiline {
  height: auto;
  padding-top: 25px;
}
.multiline > .expander {
  white-space: pre-wrap;
  visibility: hidden;
  min-height: 25px;
  word-wrap: break-word;
}
.multiline > .expander,
.multiline textarea {
  font-size: 14px;
  line-height: 20px;
  padding-bottom: 8px;
  padding-left: 1px;
  padding-right: 1px;
  word-break: break-word;
}
/* hover */
.container > textarea:hover,
.container > input:hover {
  border-color: #37cc9a;
  -webkit-transition: border-color 100ms ease;
  transition: border-color 100ms ease;
}
/* focus */
.container > textarea:focus::-webkit-input-placeholder,
.container > input:focus::-webkit-input-placeholder {
  opacity: 1;
  color: #a0a0a0;
  font-size: 14px;
}
.container > textarea:focus:-ms-input-placeholder,
.container > input:focus:-ms-input-placeholder {
  opacity: 1;
  color: #a0a0a0;
  font-size: 14px;
}
.container > textarea:focus::-ms-input-placeholder,
.container > input:focus::-ms-input-placeholder {
  opacity: 1;
  color: #a0a0a0;
  font-size: 14px;
}
.container > textarea:focus::placeholder,
.container > input:focus::placeholder {
  opacity: 1;
  color: #a0a0a0;
  font-size: 14px;
}
.container > textarea:focus,
.container > input:focus {
  border-color: #37cc9a;
  outline: none;
  -webkit-transition: border-color 100ms ease;
  transition: border-color 100ms ease;
}
.container > textarea:focus ~ label,
.container > input:focus ~ label {
  color: #37cc9a;
  font-size: 12px;
  top: 0;
}
/* icon */
.icon {
  padding-top: 22px;
  margin-right: 16px;
  color: #a0a0a0;
}
.active .icon {
  color: #a2a2a2;
}
.displayPasswordIcon {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  bottom: 8px;
  cursor: pointer;
  right: 8px;
  outline: none;
  position: absolute;
}
/* error */
.error textarea:focus ~ label,
.error input:focus ~ label,
.error .secondaryText,
.error .contentPresent {
  color: #c2143e;
}
.error textarea,
.error input,
.error textarea:hover,
.error input:hover,
.error textarea:focus,
.error input:focus {
  border-color: #c2143e;
}
/* disabled */
.disabled,
.container > input:disabled,
.container > textarea:disabled {
  cursor: not-allowed;
}
.container > textarea:disabled,
.container > input:disabled {
  border-color: var(--color-light-iron-40);
  color: #d4d4d4;
}
.disabled .secondaryText,
.container > textarea:disabled ~ label,
.container > input:disabled ~ label {
  color: #d4d4d4;
}
.disabled .icon {
  color: #d4d4d4;
}



