  text-transform: uppercase;
  font-size: 14px;
  margin-bottom: 16px;
}

.cardContent > * {
  margin-top: 8px;
}

.cardContent > *:first-child {
  margin-top: unset;
}

.inputs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.inputs > * {
  margin-left: 8px;
  -webkit-box-flex: 0;
      -ms-flex: 0;
          flex: 0;
}

.inputs > *:first-child {
  margin-left: unset;
}

.searchField {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.selectedOptionsTitle {
  font-size: 14px;
  text-transform: uppercase;
  color: #a0a0a0;
  width: 100%;
}

.selectedOptionsTags > * {
  margin-right: 8px;
  margin-bottom: 8px;
}

.selectedOptionsTags > *:last-child {
  margin-right: unset;
}

.filtersTitle {
  width: 100%;
  margin: 0 0 8px;
}

.allowOverflow {
  overflow: visible;
  overflow: initial;
}



