  max-width: 375px;
  font-size: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 80%;
  min-height: 550px;
}

@media screen and (max-width: 1024px) {

  .columnContainer {
    height: 80vh;
  }
}

.secondaryContent {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #a0a0a0;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}

.primaryContent {
  color: #ffffff;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.secondaryContent,
.primaryContent {
