  font-weight: normal;
  color: #454545;
  margin: 0 0 13px;
  width: 100%;
  line-height: 1;
  text-transform: uppercase;
}

.uppercase {
  text-transform: uppercase;
  color: #757575;
}

.uppercase > p > span {
  display: block;
  width: 100%;
}

.paragraph {
  line-height: 1.7;
}

.login .paragraph {
  text-transform: uppercase;
}

.logo {
  width: 100%;
  height: 95px;
}

.login,
.signIn {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center
