.login .paragraph, .signIn .paragraph {
  text-transform: uppercase;
}

.hugeButton,
.hugeButton button {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  font-size: 16px;
  height: 44px;
  color: #ffffff;
