/* stylelint-disable selector-no-qualifying-type */

.primaryContent .actions button[role="link"] {
  padding: 10px 0;
  text-transform: uppercase;
  color: #757575;
  font-size: 12px
}

.primaryContent .actions button[role="link"]:hover,
  .primaryContent .actions button[role="link"]:focus {
  color: #bababa;
}

.primaryContent .login > *:not(:first-child), .primaryContent .signIn > *:not(:first-child) {
  padding-top: 48px;
