@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-bold.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-bold.woff") format("woff");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: "Assistant";
  src:
    url("./../webfonts/assistant/assistant-regular.woff2") format("woff2"),
    url("./../webfonts/assistant/assistant-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/* stylelint-disable */

body {
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  color: #a0a0a0;
}

body * {
  letter-spacing: 0.02em;
}

h1,
h2,
h3,
h4,
h5,
h6,
.display,
.headline {
  font-family: "Assistant", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #454545;
}

h1 {
  font-size: 24px;
  margin: 24px 0;
}

h2 {
  font-size: 20px;
  margin: 24px 0;
}

h3 {
  font-size: 18px;
  margin: 16px 0;
}

p {
  margin-bottom: 1em;
}

a,
[role=link] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  display: inline;
  padding: 0;
  cursor: pointer;
  color: #37cc9a;
  text-decoration: unset;
}

a:hover {
  text-decoration: underline;
}

ul,
ol {
  padding: 0 24px;
}

li {
  list-style-type: disc;
}

blockquote {
  border-radius: 2px;
  border-left: 5px solid #37cc9a;
  padding-left: 16px;
  margin: 0;
}

/* stylelint-enable */

.dateInput,
.dateInput * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}

.dateInput {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1px 1px 1px 10px;
  white-space: nowrap;
  height: 28px;
  background-color: #ffffff;
  border-radius: 3px;
  border: 1px solid #c2c5c7;
  font-family: "Assistant", sans-serif;
  font-size: 14px;
  -webkit-transition: border-color 100ms ease-in-out;
  transition: border-color 100ms ease-in-out;
  position: relative;
  vertical-align: text-top
}

.dateInput.active,
  .dateInput.focus {
  border-color: #37cc9a;
}

.dateInput.error {
  border-color: rgba(220,104,96, 1);
}

.dateSelector {
  position: absolute;
  top: calc(100% + 3px);
  left: -1px;
  z-index: 10;
}

.start,
.end {
  position: relative;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-right: 5px;
  padding-left: 5px
}

.start.active,
  .start.focus,
  .end.active,
  .end.focus {
  background-color: #c3f0e0;
}

.start.error, .end.error {
  background-color: #f3d0d8;
}

.icon {
  color: #a0a0a0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 16px;
  margin-right: 5px;
}

.error .icon {
  color: rgba(220,104,96, 1);
}

.active .icon,
.focus .icon {
  color: #37cc9a;
}

.input,
.expander {
  border: 0;
  padding: 0;
  white-space: pre;
  font-size: 14px;
  color: #757575;
  font-family: "Assistant", sans-serif;
}

.expander {
  visibility: hidden;
}

.input:placeholder {
  color: #757575;
}

.input {
  position: absolute;
  top: 0;
  left: 5px;
  width: calc(100% - 5px);
  height: 100%;
  background-color: transparent;
}

.separator {
  background: #757575;
  width: 1px;
  height: 20px;
  margin: 0 1px;
}

.active .separator,
.focus .separator {
  background-color: #37cc9a;
}

.error .separator {
  background-color: rgba(220,104,96, 1);
}




